@import url(https://fonts.googleapis.com/css2?family=Exo:wght@200&display=swap);
.Persik {
  display: block;
  width: 50%;
}

.mainblock{
  align-items: center;
  justify-content: center;
  margin: 20% auto;
}

.button{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: 20% auto;
}

.score{
  font-family: 'Exo', sans-serif;
  font-size: 70px;
}
.scoresecond{
  margin-top: -15px;
}

.parent {
display: grid;
grid-template-columns: 1fr;
grid-template-rows: 15px 70px 15px;
grid-column-gap: 0px;
grid-row-gap: 0px;
}

.div1 { grid-area: 1 / 1 / 2 / 2; }
.div2 { grid-area: 2 / 1 / 3 / 2; }
.div3 { grid-area: 3 / 1 / 4 / 2; }

